import { IStateMachine } from "../../state-machine/IStateMachine";
import { StateMachine } from "../../state-machine/StateMachine";
import { BattleState } from "./states/BattleState";
import { BuildState } from "./states/BuildState";
import { FinishState } from "./states/FinishState";
import { PositioningState } from "./states/PositioningState";

export class GameStateMachine extends StateMachine implements IStateMachine {

  constructor(model: any, stateEnum: any) {
    super(model, stateEnum);
  }

  init(stateEnum: any): void {
    super.init(stateEnum);

    this.addState(BuildState, stateEnum.BUILDING);
    this.addState(PositioningState, stateEnum.POSITIONING);
    this.addState(BattleState, stateEnum.BATTLE);
    this.addState(FinishState, stateEnum.FINISH);
  }
}